* {
    box-sizing: border-box;
}

body {
    margin: 0;
    overflow: hidden; /* Hide scrollbars */
    background-color: black;
}

.container {
    margin: 1rem;
    text-align: center;
}


.nav {
    background-color: hsl(0, 0%, 20%,0);
    color: white;
    position: fixed;
    top: auto;
    z-index: 999;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
    width: 100%;
}

.site-title {
    font-size:1rem ;
    opacity: 50%;
}


.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 3rem;
    font-size: 1rem;
    opacity: 80%;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}


.nav li.active {
    background-color: #555;
}

.nav li:hover {
    background-color: #777;
}

.container {
    margin: 0;
    padding: 0;
}