.contact-bg {
    background-color: black;
    height: 100vh;
    width: 100%;
}

.contact-bg h1 {
    margin: 0;
    padding: 0;
}

.contact-bg video{
    height: 100vh;
    width: 100%;
    object-fit: fill;
    top: 100%;
    position: static;
}
.contact-bg img {
    margin-top: 20px;
    height: 100px;
    opacity: 90%;
}

.contact-bg button {
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    background-color: #e7e7e7; color: black;
    letter-spacing: 1.5px;
    border-radius: 10px;
    margin-top: 20px;
}

.contact-container {
    position: absolute;
    z-index: 900;
    color: white;
    bottom: 10%;
    left: 10%;
    right: 10%;
    background-color: hsl(0, 0%, 100%);
    background-color: hsl(0, 0%, 100%,30%);
    margin: 0;
    padding: 0;
    text-align: center;
    height: 30vh;
    width: 20%;
    min-width: 400px;
    min-height: 30vh;
    margin: auto;
    object-fit: fill;
    border-radius: 10%;
    display: block;
}

#button {
    opacity: 90%;
}