.about-bg {
    background-color: black;
    height: 100vh;
    width: 100%;
}

.about-bg h1 {
    margin: 0;
    padding: 0;
    margin-top: 30px;
    color: black;
    opacity: 95%;
}

.about-bg p {
    color: black;
    font:sans-serif;
    font-weight: normal;
    letter-spacing: -1px;
    font-size: 1rem;
    opacity: 100%;
}

.about-bg img {
    margin-top: 20px;
    margin-bottom: 20px;
    height: 150px;
    opacity: 95%;
}

.about-bg video{
    height: 100vh;
    width: 100%;
    object-fit: fill;
    top: 100%;
    position: static;
}


.about-container {
    position: absolute;
    color: white;
    top: 10%;
    left: 10%;
    right: 10%;
    background-color: hsl(0, 0%, 100%);
    background-color: hsl(0, 0%, 100%,20%);
    margin: 0;
    padding: 0;
    text-align: center;
    height: 80vh;
    width: 50%;
    min-width: 400px;
    margin: auto;
    object-fit: fill;
    border-radius: 10%;
}

