.home-bg {
    background-color: black;
    height: 100vh;
    width: 100%;
}

.home-bg h1 {
    margin: 0;
    padding: 0;
}

.home-bg video{
    height: 100vh;
    width: 100%;
    object-fit: fill;
    top: 100%;
    position: static;
}

.overlay {
    position: absolute;
    color: white;
    top: 35%;
    background-color: hsl(60, 100%, 50%,0);
    margin: 0;
    padding: 0;
    font-size: 6rem;
    text-align: center;
    height: 100vh;
    width: 100%;
    object-fit: fill;
    font-style: italic;
    letter-spacing: 10px;
    text-shadow: 10px;
    font-weight: lighter;
    font-family: "Trebuchet MS", Verdana, sans-serif;
    opacity: 50%;
    text-decoration:overline;
}

.overlay2 {
    
    position: absolute;
    color: white;
    top: 50%;
    background-color: hsl(60, 100%, 50%,0);
    margin: 0;
    padding: 0;
    font-size: 2rem;
    text-align: center;
    height: 100vh;
    width: 100%;
    object-fit: fill;
    font-style: italic;
    letter-spacing: 5px;
    text-shadow: 10px;
    font-weight: lighter;
    font-family: serif;
    opacity: 60%;
}
